*{
    font-family: 'poppins', 'Orbitron', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: clip;
}

body{
    overflow-x: hidden;
}

div{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

